@import url(https://rsms.me/inter/inter.css);
html, body, #root {
  width: 100%;
  height: 100vh;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black;
}

#root {
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Video {
   z-index: -1;
   position: fixed;
   top: 0;
   bottom: 0;
   width: 100%;
   height: 100%; 
   overflow: hidden;
   -webkit-animation-name: blur;
           animation-name: blur;
   -webkit-animation-duration: 3s;
           animation-duration: 3s;
   -webkit-animation-delay: 3s;
           animation-delay: 3s;
   -webkit-animation-fill-mode: forwards;
           animation-fill-mode: forwards;
}

video#rms {
   min-width: 100%;
   min-height: 100%;
   width: auto;
   height: auto;
   position: absolute;
   top: 50%;
   left: 50%;
   transform: translate(-50%,-50%);
   z-index: -10;
}

@-webkit-keyframes blur {
   to {
      -webkit-filter: blur(20px);
              filter: blur(20px);
   }
}

@keyframes blur {
   to {
      -webkit-filter: blur(20px);
              filter: blur(20px);
   }
}
.Menu {
   z-index: 0;
   position: fixed;
   flex: 0 1 auto;
   width: 40px;
   margin: 170px 30px 0 40px;
   -webkit-user-select: none; /* Safari */
   -moz-user-select: none; /* Firefox */
   -ms-user-select: none; /* IE10+/Edge */
   user-select: none; /* Standard */
}

.MenuLogo {
   transition: opacity .5s;
   opacity: 0;
   position: absolute;
   top: -130px;
   left: 70px;
}

.Menu_opened .Menu {
   z-index: 1;
}

.Menu_opened .Menu .MenuLogo {
   opacity: 1;
}

.MenuItems {
   position: fixed;
   transition: opacity .5s;
   opacity: 0;
}

.Menu_opened .MenuItems {
   opacity: 1;
}

.Menu_opened .MenuItems li span {
   cursor: pointer;
}

.MenuItems ul {
   margin: 0;
   padding: 0;
   list-style: none;
}

.MenuItems li {
   display: flex;
   align-items: center;
   font-weight: bold;
   font-size: 72px;
   height: 100px;
}

.MenuItems li::before {
   display: inline-block;
   content: '';
   width: 40px;
   height: 10px;
   background-color: white;
   margin-right: 30px;
}

.MenuButton {
   position: fixed;
   top: 65px;
   opacity: 0;
   -webkit-animation-name: fadein;
           animation-name: fadein;
   -webkit-animation-duration: 3s;
           animation-duration: 3s;
   -webkit-animation-delay: 4s;
           animation-delay: 4s;
   -webkit-animation-fill-mode: forwards;
           animation-fill-mode: forwards;
   cursor: pointer;
   width: 40px;
   height: 40px;
   transform: rotate(0deg);
   transition: all .25s ease-in-out;
}

.MenuButton span {
   width: 40px;
   height: 10px;
   background-color: white;
   display: block;
   position: absolute;
   transform: rotate(0deg) translate(0, 0);
   transition: all .25s ease-in-out;
   left: 0;
   opacity: 1;
}

.MenuButton span:nth-child(2) {
   top: 0px;
}

.MenuButton span:nth-child(2) {
   top: 20px;
}

.MenuButton_opened span:nth-child(1) {
   transform: rotate(45deg) translate(3px, 10px);
}

.MenuButton_opened span:nth-child(2) {
   transform: rotate(-45deg) translate(4px, -11px);
}

.MenuInfo {
   bottom: 20px;
}

.MenuInfo div {
   margin-bottom: 7px;
}

@-webkit-keyframes fadein {
   to {
      opacity: 1;
   }
}

@keyframes fadein {
   to {
      opacity: 1;
   }
}

.RentTabs {
   display: flex;
   font-weight: bold;
   font-size: 72px;
   line-height: 86px;
}

.RentTabs_tab {
   cursor: pointer;
   margin-right: 0.2em;
   opacity: 0.5;
}

.Tab-enter {
   opacity: 0;
}

.Tab-enter-active {
   opacity: 0.5;
   transition: all 500ms ease-out;
}

.Tab-enter:first-child,
.Tab-enter-done:first-child,
.Tab-appear:first-child,
.Tab-appear-done:first-child {
   opacity: 1;
}

.Tab-exit {
   opacity: 0.5;
   width: 100%;
}

.Tab-exit-active {
   margin: 0;
   width: 0;
   opacity: 0;
   transition: all 500ms ease-out;
}

.Rent {
   position: relative;
   flex: 1 1 auto;
   padding-top: 160px;
   opacity: 0;
   -webkit-animation-name: fadein;
           animation-name: fadein;
   -webkit-animation-duration: 1.5s;
           animation-duration: 1.5s;
   -webkit-animation-fill-mode: forwards;
           animation-fill-mode: forwards;
}

.Logo_text {
   display: flex;
}

.RentLogo {
   margin-right: 0.2em;
}

.RentObjects_container {
   position: relative;
}

.RentObjects {
   display: flex;
   flex-wrap: wrap;
}

.RentObject {
   display: flex;
   margin-bottom: 40px;
   margin-right: 24px;
}

.RentObject_img {
   width: 180px;
   height: 180px;
}

.RentObject_content {
   width: 340px;
   margin-left: 16px;
   line-height: 24px;
}

.RentObject_content-caption {
   font-size: 20px;
   font-weight: bold;
   margin-bottom: 16px;
}

.RentObject_content-text {
   font-size: 18px;
}

.RentObjectsArea-enter {
   opacity: 0;
   position: absolute;
   top: 0;
   left: 0;
}

.RentObjectsArea-enter-active {
   opacity: 1;
   transition: all 1000ms ease-in-out;
}

.RentObjectsArea-exit {
   opacity: 1;
}

.RentObjectsArea-exit-active {
   opacity: 0;
   transition: all 100ms ease-in-out;
}
.Services {
   flex: 1 1 auto;
   padding-top: 160px;
   opacity: 0;
   -webkit-animation-name: fadein;
           animation-name: fadein;
   -webkit-animation-duration: 1.5s;
           animation-duration: 1.5s;
   -webkit-animation-fill-mode: forwards;
           animation-fill-mode: forwards;
}

.Services_text {
   max-width: 720px;
   line-height: 24px;
   margin-bottom: 400px;
}

.Services_text h4 {
   font-family: Inter;
   font-style: normal;
   font-weight: normal;
   font-size: 30px;
   line-height: 135%;
}

.Services_text ul {
   list-style: none;
   -webkit-padding-start: 0;
           padding-inline-start: 0;
   margin-left: 5px;
}

.Services_text ul li::before {
   content:  "—";
   position: relative;
   left: -5px;
}

.Services_menu {
   position: fixed;
   top: 40px;
   right: 30px;
   max-width: 440px;
   display: flex;
   flex-direction: row;
   -webkit-padding-start: 0;
           padding-inline-start: 0;
   flex-wrap: wrap;
   justify-content: flex-start;
   align-items: flex-start;
}

.Services_menu-item {
   border: 4px solid white;
   margin: 0 12px 12px 0;
   background: white;
   color: black;
   cursor: pointer;
   font-weight: bold;
   font-size: 24px;
   padding: 0 7px;
}

.Services_menu-item span {
   word-wrap: break-word;
   white-space: normal;
}

.Services_menu-item#streams {
   max-width: 77%;
}

.Services_menu-item:hover {
   color: white;
   background: black;
}

.Services_menu .Services_menu-active {
   color: white;
   background: black;
}

.Main {
   height: 100vh;
   flex: 1 1 auto;
   opacity: 0;
   -webkit-animation-name: fadein;
           animation-name: fadein;
   -webkit-animation-duration: 2s;
           animation-duration: 2s;
   -webkit-animation-delay: 5s;
           animation-delay: 5s;
   -webkit-animation-fill-mode: forwards;
           animation-fill-mode: forwards;
   display: flex;
   flex-direction: column;
   justify-content: center;
}

.Main .Text {
   max-width: 850px;
   line-height: 24px;
}

.Main .Text h1 {
   font-size: 72px;
   line-height: 86px;
}

.Main .Text p {
   font-size: 30px;
   line-height: 135%;
}

.Carousel {
  -webkit-animation: scroll 40s linear infinite;
          animation: scroll 40s linear infinite;
  display: flex;
  flex-direction: column;
  height: calc(100px * 42);
  overflow: hidden;
}

.Carousel_item {
   height: 100px;
   display: flex;
   justify-content: center;
}

.Carousel_item img {
   height: 50px;
}

@-webkit-keyframes scroll {
   0% {
      transform: translateY(calc(-100px * 21));
   }
   100% {
      transform: translateY(0px);
   }
}

@keyframes scroll {
   0% {
      transform: translateY(calc(-100px * 21));
   }
   100% {
      transform: translateY(0px);
   }
}

.About {
   flex: 1 1 auto;
   padding-top: 160px;
   opacity: 0;
   -webkit-animation-name: fadein;
           animation-name: fadein;
   -webkit-animation-duration: 1.5s;
           animation-duration: 1.5s;
   -webkit-animation-fill-mode: forwards;
           animation-fill-mode: forwards;
}

.About_text {
   max-width: 720px;
   margin-bottom: 40px;
   line-height: 24px;
}

.About_offer {
   display: flex;
   cursor: pointer;
   height: 50px;
   align-items: center;
}

.About_offer-text {
   margin-left: 24px;
   font-weight: bold;
   font-size: 32px;
}

.About_carousel {
   position: fixed;
   top: 0;
   right: 130px;
   width: 200px;
   height: 100%;
   overflow: hidden;
}

@supports (font-variation-settings: normal) {
   .App {
      font-family: 'Inter var', sans-serif;
   }
}

.App {
   position: absolute;
   top: 0;
   bottom: 0;
   left: 0;
   right: -2%;
   display: flex;
   font-family: 'Inter', sans-serif;
   font-style: normal;
   font-weight: normal;
   font-size: 18px;
   color: #ffffff;
   text-align: left;
   overflow-y: scroll;
   overflow-x: hidden;
}

.App a {
   color: #ffffff;
   text-decoration: none;
}

.App h3 {
   font-size: 32px;
   line-height: 40px;
}

.App li {
   line-height: 32px;
}

.Logo {
   position: absolute;
   -webkit-animation-duration: 3s;
           animation-duration: 3s;
   -webkit-animation-name: slidein;
           animation-name: slidein;
   -webkit-animation-delay: 3s;
           animation-delay: 3s;
   top: 50vh;
   left: 110px;
   width: 180px;
   -webkit-animation-fill-mode: forwards;
           animation-fill-mode: forwards;
}

.LogoMenu_opened {
   opacity: 0;
   transition: opacity .5s;
}

.LogoMenu_closed {
   opacity: 1;
}

.Logo_text {
   position: absolute;
   top: 40px;
   left: 200px;
   font-weight: bold;
   font-size: 72px;
   line-height: 86px;
}

.Content {
   width: 100%;
}

.ActiveArea {
   position: relative;
   margin-left: 110px;
   opacity: 1;
   flex: 1 1 auto;
   transition: opacity 0.5s;
   height: 100%;
}

.Menu_opened .ActiveArea {
   opacity: 0;
}

@-webkit-keyframes slidein {
   to {
      top: 40px;
   }
}

@keyframes slidein {
   to {
      top: 40px;
   }
}

